.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.title{
  font-size: 20px;
}

ul, li{
  list-style: none;
  padding: 0;
  margin: 0;

}
button{
  background: inherit ;
   border:none; 
   box-shadow:none; 
   border-radius:0;
    padding:0; 
    overflow:visible; 
    cursor:pointer
  } 

  #add-button{
      font-size: 30px;
      color: rgb(214, 214, 214);
  }

  #todo-list{
  padding: 20px;

  

  
  }
    


.date{
  display: flex;
  background: linear-gradient(white , lightCyan);
  padding: 10px;
  line-height: 24px;
  border-bottom: 2px solid lightblue;
}

.date .left{
  width: 70%;
  text-align: left;
}

.date .right{
  font-size: 13px;
  width: 30%;
  text-align: right;

}

.layer{
  width: 100%;
  padding: 10px;
  display: block;
  border-bottom: 2px solid lightblue;
  
}



.container{
  width: 500px;
  margin: 0 auto;
  text-align: center;
  border-radius: 5px;


}

.todo-input {
  width: 80%;
text-align: center;
border: 0;
outline: none;
font-size: 1.3rem;
}

.modal{
  margin-top : 20px;
  padding : 20px;
  background : #eee;
  text-align : left;
}